import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";

import Icon from "../../components/common/Icon";

import * as Actions from "../../actions/user";

import {
  Wrapper,
  FormContainer,
  Form,
  FormTitle,
  FormItem,
  Input,
  FormBtn,
  FormTip,
} from "./styles";

const initLoginData = {
  type: "login",
  dataArr: [
    {
      name: "account",
      icon: "account",
    },
    {
      name: "password",
      icon: "lock",
    },
  ],
  result: null,
};

const initRegisterData = {
  type: "register",
  dataArr: [
    {
      name: "account",
      icon: "account",
    },
    {
      name: "email",
      icon: "email1",
    },
    {
      name: "password",
      icon: "lock",
    },
  ],
  result: null,
};

const Login = () => {
  const [formData, setFormData] = useState(initLoginData);
  const { type, dataArr, result } = formData;
  const dispatch = useDispatch();
  const history = useHistory();

  const handleSubmit = () => {
    console.log("handleSubmit---", type, result, history);
    dispatch(
      Actions[type](result, history, (type) => {
        toggleType(type);
      })
    );
  };

  const handleFormChange = (e) => {
    setFormData({
      ...formData,
      result: {
        ...formData.result,
        [e.target.name]: e.target.value,
      },
    });
  };
  const toggleType = (type) => {
    setFormData(type === "login" ? initLoginData : initRegisterData);
  };

  return (
    <Wrapper>
      <FormContainer className={type}>
        <Form className={type}>
          <FormTitle>blog</FormTitle>
          {dataArr.map(({ name, icon }, index) => (
            <FormItem key={index}>
              <Icon type={`icon-${icon}`} />
              <Input
                onChange={handleFormChange}
                placeholder={name}
                value={result?.name}
                type={name}
                name={name}
              />
            </FormItem>
          ))}
          <FormBtn onClick={handleSubmit}>{type}</FormBtn>
          <FormTip
            onClick={() => toggleType(type === "login" ? "register" : "login")}
          >
            {type === "login" ? "创建新账号？" : "去登录"}
          </FormTip>
        </Form>
      </FormContainer>
    </Wrapper>
  );
};

export default Login;
