import styled from "styled-components";

export const Wrapper = styled.div`
  position: relative;
  width: 100%;
  min-height: 100vh;
  overflow: auto;
`;
export const FormContainer = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  &::before {
    content: "";
    position: absolute;
    height: 4000px;
    width: 4000px;
    top: -10%;
    right: 50%;
    transform: translateY(-50%);
    background-color: ${(props) => props.theme.t_blue};
    /* background-image: linear-gradient(-45deg, #4481eb 0%, #04befe 100%); */
    border-radius: 50%;
    transition: 1.4s ease-in-out;
    z-index: 2;
  }
  &.register {
    &::before {
      transform: translate(100%, -50%);
    }
  }

  @media (max-width: 1000px) {
    &::before {
      height: 1000px;
      width: 1000px;
      top: 10%;
    }

    &.register {
      &::before {
        transform: translate(0, 10%);
      }
    }
  }
  @media (max-width: 800px) {
    &::before {
      width: 1500px;
      height: 1500px;
      right: -30%;
      top: -50%;
    }

    &.register {
      &::before {
        top: 50%;
      }
    }
  }
  @media (max-width: 540px) {
    &::before {
      width: 800px;
      height: 800px;
      left: 50%;
      transform: translate(-50%, -40%);
    }
    &.register {
      &::before {
        transform: translate(-50%, 20%);
      }
    }
  }
`;

export const Form = styled.div`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.5s ease-in-out;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0rem 5rem;

  /* min-width: 340px; */

  /* transition: all 0.2s 0.7s; */
  overflow: hidden;
  z-index: 1;
  &.login {
    left: 50%;
  }
  &.register {
    left: 0;
  }
  @media (max-width: 1000px) {
    padding: 0 40px;
    &.register {
      left: 50% !important;
    }
  }
  @media (max-width: 800px) {
    bottom: 10%;
    transform: translateX(-50%);
    padding: 0;
    &.login {
      left: 50%;
    }
    &.register {
      left: 0;
      top: -20%;
    }
  }
  @media (max-width: 540px) {
    bottom: 0;
    width: 80%;
    padding-bottom: 20px;
  }
`;

export const FormTitle = styled.div`
  font-weight: 500;
  font-size: 2.2rem;
  color: ${(props) => props.theme.t_textColor};
  margin-bottom: 10px;
  @media (max-width: 540px) {
    font-size: 1.8rem;
  }
`;

export const FormItem = styled.div`
  background-color: #f0f0f0;
  max-width: 380px;
  width: 100%;
  height: 55px;
  border-radius: 55px;
  margin: 10px 0;
  padding: 0 1rem;
  display: flex;

  align-items: center;

  > span {
    font-size: 24px;
    margin-right: 10px;
    flex-shrink: 0;
  }
  @media (max-width: 540px) {
    height: 0;
    padding: 20px 24px;
    > span {
      font-size: 18px;
    }
  }
`;

export const Input = styled.input`
  flex: 1;
  color: #333;
`;

export const FormBtn = styled.button`
  background-color: ${(props) => props.theme.t_blue};
  padding: 12px 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
  margin: 10px 0;
  transition: 0.5s;
  &:hover {
    cursor: pointer;
    opacity: 0.8;
  }
  &.fail {
    background: #333;
    opacity: 0.5;
  }
  @media (max-width: 540px) {
    padding: 10px 40px;
    font-size: 12px;
  }
`;

export const FormTip = styled.div`
  padding: 0.7rem 0;
  font-size: 0.5rem;
  color: ${(props) => props.theme.t_textColor};
  &:hover {
    cursor: pointer;
    text-decoration: underline;
  }
  @media (max-width: 540px) {
    font-size: 12px;
  }
`;

export const PanelContainer = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
`;

export const Panel = styled.div`
  position: absolute;
  width: 50%;
  height: 100%;
  transition: 1.4s ease-in-out;
  z-index: 4;
  &.login {
    right: 50%;
  }
  &.register {
    right: 0;
  }
`;
